<template>
  <div class="panel" :class="[pageType]" @click="select(item)">
    <div class="cover">
      <img :src="item.cover" alt="封面">
    </div>
    <div class="brief">
      <span class="title">{{item.title}}</span>
      <span class="author">{{item.author}}</span>
    </div>
    <div class="stamp">
      <span class="time">{{item.time}}</span>
      <span class="status" :status="item.status">{{parseStatus(item.status)}}</span>
    </div>
  </div>
</template>

<script type="ecmascript-6">
export default {
  props: {
    pageType: {
      type: String,
      default: 'vote',
      required: true
    },
    item: {
      type: Object,
      required: true
    },
    status: {
      type: Array,
      required: true
    }
  },
  methods: {
    select(item) {
      this.$emit('select', item);
    },
    parseStatus(statusIndex) {
      return this.status[statusIndex];
    }
  }
}
</script>

<style lang="stylus" scoped>
.panel
  background-color #fff
  display flex
  padding 0.2rem
  border-radius 0.05rem
  margin-bottom 0.2rem
  &:last-child
    margin-bottom 0.1rem
  &.survey .stamp .status[status="3"]
    color #b42632
  .cover
    margin-right 0.1rem
    img
      width 1.2rem
      height 1.2rem
      background-color #ccc
  .brief
    flex-grow 0
    display flex
    flex-direction column
    justify-content space-between
    flex-grow 1
    .title
      width 3.4rem
      overflow hidden
      white-space nowrap
      text-overflow ellipsis
      font-size 0.34rem
    .author
      font-size 0.3rem
      color #999
  .stamp
    flex-shrink 0
    display flex
    flex-direction column
    justify-content space-between
    .time
      font-size 0.3rem
      color #999
    .status
      font-size 0.28rem
      text-align right
      &[status="0"]
        color #fd7c14
      &[status="1"]
        color #2dc56d
      &[status="2"]
        color #d62525
      &[status="3"]
        color #b42632
      &[status="4"]
        color #b42632
</style>




