import axios from 'axios'
import { commonParam, debug, DEV_HOST } from './config'

// 开发环境设置主机地址
debug && (axios.defaults.baseURL = DEV_HOST)

// 请求之前设置请求参数 aid
axios.interceptors.request.use(function(config) {

  config.data = Object.assign({}, config.data, commonParam)

  return config
}, function(err) {
  return Promise.reject(err)
})

export default axios



