import axios from './axios';

// 调研列表
export function getSurveyList(option) {
  const url = '/weixin/survey/lists';

  return axios.post(url, option);
}

// 调研详情
export function getSurveyDetail(option) {
  const url = '/weixin/survey/detail';

  return axios.post(url, option);
}

// 提交调研
export function submitSurvey(option) {
  const url = '/weixin/survey/answer';

  return axios.post(url, option);
}

// 已参与人员 -- 管理员
export function getSurveyHadJoin(option) {
  const url = '/weixin/survey/getJoinMem';

  return axios.post(url, option);
}

// 未参与人员 -- 管理员
export function getSurveyNotJoin(option) {
  const url = '/weixin/survey/getUnjoinMem';

  return axios.post(url, option);
}

// 提醒参与 -- 管理员
export function remindSurvey(option) {
  const url = '/weixin/survey/remind';

  return axios.post(url, option);
}



