<template>
  <scroll class="scroll-wrapper" :data="surveyList" :pullup="true"  @scrollToEnd="getMore" ref="scroll">
    <div class="scroll-content">
      <transition-group name="fade-slide-up" tag="div">
        <panel page-type="survey"
              v-for="item in surveyList"
              :key="item.id"
              :item="item"
              :status="statusText"
              @select="selectItem"
        />
      </transition-group>
      <load-more :over="!hasMore" :is-loading="isLoading"/>
    </div>
  </scroll>
</template>

<script type="ecmascript-6">
import Scroll from '@/base/scroll/scroll';
import Panel from '@/base/panel/vote-survey-list';
import LoadMore from '@/base/load-more/load-more';

import { getSurveyList } from '@/api/survey';
import { AID, ERR_OK, LOGIN_EXPIRE } from '@/api/config';
import { SURVEY_STATE_TEXT } from '@/common/js/config';

import { mapMutations } from 'vuex';

export default {
  data() {
    return {
      surveyList: [],
      hasMore: true,
      isLoading: false,
    };
  },
  created() {
    this.offset = 0;
    this.limit = 10;

    this.statusText = SURVEY_STATE_TEXT;
    this._getSurveyList();
  },
  activated() {
    setTimeout(() => {
      this.$refs.scroll.refresh()
    }, 20)
  },
  methods: {
    // 获取列表
    _getSurveyList() {
      this.isLoading = true;
      getSurveyList({  limit: this.limit, offset: this.offset })
        .then((res) => {
          if (res.data.code === ERR_OK) {

            this.surveyList = this.surveyList.concat(res.data.data.list);

            this.isLoading = false;

            if (res.data.data.list < this.limit) {
              this.hasMore = false;
            }

            this.isAdmin = Number(res.data.data.isAdmin) === 1;

            this.setUserType({ isAdmin: this.isAdmin });

            this.setSurveyList(this.surveyList);
          }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 加载更多
    getMore() {
        if (!this.hasMore) {
          return
        }
        this.offset += 10;
        this._getSurveyList();
      },
    //选中列表
    selectItem(item) {
      if ((Number(item.status) === 0 || Number(item.status) === 2) && !this.isAdmin) {
        this.$dialog.notify({
          mes: `抱歉！此调研${this.statusText[item.status]}`,
          timeout: 1500
        });
      } else {
         this.$router.push(`/survey/${item.id}/detail/aid/${AID}`);

        //  this.setSurveyItem(item);
      }
    },

    ...mapMutations({
      setUserType: 'SET_USER_TYPE',
      setSurveyList: 'SET_SURVEY_LIST'
      // setSurveyItem: 'SET_SURVEY_ITEM'
    })
  },
  components: {
    Scroll,
    LoadMore,
    Panel
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/common/stylus/mixin';

.scroll-wrapper
  position absolute
  top 0
  left 0
  right 0
  bottom 0
  padding 0 0.24rem 0 0.26rem
  overflow hidden
  .scroll-content
    animation-fade-slide-up()

</style>




