
const matchAid = /\/aid\/([0-9]+)(?:\/|$)/.exec(window.location.href);
const aid = matchAid ? matchAid[1] : '00000000';
/**
 * global
 */
export const debug = process.env.NODE_ENV !== 'production';

export const commonParam = debug ? { aid, adminid: 10 } : { aid };

export const AID = aid;

export const ERR_OK = 200;

export const LOGIN_EXPIRE = 402;

export const DEV_HOST = 'http://zj.weiwojiaoyu.com'



