<template>
  <div class="loading">
    <div class="no-more" v-if="over">
     <span class="line"></span>
     <span class="no-more-tips">{{overTip}}</span>
     <span class="line"></span>
    </div>
    <div class="load-more" v-else>
        <img class="load-more-img" v-show="isLoading" src="./loading.gif">
        <span class="load-more-tips">{{loadingTip}}</span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    props: {
      isLoading: {
        type: Boolean,
        default: false
      },
      over: {
        type: Boolean,
        default: false
      },
      overTip: {
        type: String,
        default: '暂无更多数据'
      }
    },
    data() {
      return {
        loadingTip: '正在加载'
      }
    },
    created() {
      this.$watch('isLoading', (newVal) => {
        if (newVal) {
          this.loadingTip = '正在加载';
        } else {
           this.loadingTip = '释放加载更多';
        }
      })
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/1px"

  .loading
    width: 100%
    text-align: center
    .no-more
      display flex
      align-items center
      width: 65%;
      margin: 0 auto;
      padding 0.2rem 0
      text-align: center;
      .no-more-tips
        font-size: 0.28rem;
        position: relative;
        padding: 0 .55em;
        display: inline-block;
        color: #999999;
      .line
        height 2px
        border-top 2px solid #ccc
        transform scaleY(0.5)
        flex-grow 1
    .load-more
      display flex
      justify-content center
      align-items center
      width: 65%;
      margin: 0 auto;
      padding 0.2rem 0
      .load-more-img
        width: 0.36rem;
        height: 0.36rem;
        margin-right 0.1rem
      .load-more-tips
        font-size: 0.28rem;
        color #212121

</style>



